.p-map {
  box-sizing: border-box;
  min-height: 706px;
  background-color: #04080c;
  padding-top: 40px;
  padding-bottom: 400px;
  margin-top: 5px;
  position: relative;
  z-index: 111; }
  .p-map::after {
    content: "";
    display: block;
    position: absolute;
    background-image: url("../img/map-mob.png");
    background-repeat: no-repeat;
    background-position: 50% top;
    top: -8px;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1; }
  .p-map__title {
    padding-bottom: 30px;
    background-image: url("../img/sabre_dec.png");
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 310px;
    line-height: 1;
    margin-bottom: 18px; }
  .p-map__intro {
    color: #003e7e;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 14px;
    line-height: 1.5;
    margin-bottom: 35px; }
  .p-map__content {
    padding: 0 10px; }
  .p-map__wrap {
    text-align: center;
    max-width: 340px;
    margin: 0 auto; }
  .p-map__stages {
    font-size: 10px;
    font-weight: bold;
    color: #7f5a3a;
    position: relative;
    margin: 0 auto;
    width: 300px; }
    .p-map__stages__item {
      position: absolute;
      line-height: 1.1; }
      .p-map__stages__item-date {
        white-space: nowrap;
        text-transform: uppercase;
        margin-bottom: 25px; }
      .p-map__stages__item-1 {
        width: 75px;
        left: 3px;
        top: 34px; }
      .p-map__stages__item-2 {
        width: 150px;
        left: 66px;
        top: 34px; }
      .p-map__stages__item-3 {
        width: 75px;
        top: 34px;
        left: 216px; }
  .p-map .button {
    width: 100%;
    max-width: 320px; }
  @media only screen and (min-width: 960px) {
    .p-map {
      min-height: auto;
      background-image: url("../img/map-960.png");
      background-repeat: no-repeat;
      background-position: left top;
      background-color: transparent;
      position: relative;
      top: -43px;
      margin-bottom: -60px;
      margin-top: 0;
      padding-top: 90px;
      padding-bottom: 185px; }
      .p-map::after {
        display: none; }
      .p-map__content {
        width: 560px; }
      .p-map__wrap {
        margin: 0 auto; }
      .p-map__intro {
        margin-bottom: 10px; }
      .p-map__stages__item-1 {
        left: 13px;
        top: 20px; }
      .p-map__stages__item-2 {
        left: 79px;
        top: 20px; }
      .p-map__stages__item-3 {
        top: 20px;
        left: 226px; }
      .p-map .button {
        width: 100%;
        padding-left: 0;
        padding-right: 0;
        max-width: 230px; } }
  @media only screen and (min-width: 1280px) {
    .p-map {
      background-image: url("../img/p-map_bg-1280.png");
      padding-top: 160px;
      padding-bottom: 220px; }
      .p-map__content {
        width: 760px; }
      .p-map__stages__item-1 {
        left: 2px; }
      .p-map__stages__item-2 {
        left: 70px; }
      .p-map__stages__item-3 {
        left: 229px; } }
  @media only screen and (min-width: 1440px) {
    .p-map {
      background-image: url("../img/map-large.png");
      padding-top: 158px;
      padding-bottom: 280px; }
      .p-map__content {
        width: 810px;
        margin-right: auto;
        margin-left: 58px; }
      .p-map__intro {
        font-size: 16px;
        max-width: 390px;
        margin: 0 auto;
        margin-bottom: 24px; }
      .p-map__wrap {
        max-width: 540px; }
      .p-map__title {
        background-size: 500px;
        padding-bottom: 46px; }
      .p-map__stages {
        font-size: 13px; }
        .p-map__stages__item-1 {
          left: -23px;
          top: 42px; }
        .p-map__stages__item-2 {
          width: 200px;
          left: 48px;
          top: 42px; }
        .p-map__stages__item-3 {
          top: 42px;
          left: 255px; }
        .p-map__stages__item-date {
          margin-bottom: 32px; }
      .p-map .button {
        width: 320px;
        max-width: 100%; } }

.map-footer {
  position: relative;
  margin-bottom: 24px;
  margin-top: 290px; }
  .map-footer__row {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between; }
  .map-footer__item {
    color: #a48550; }
  @media only screen and (min-width: 960px) {
    .map-footer {
      display: none; } }
  .map-footer .btn-scroll-to {
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%); }
